













































































































































































































.auth-container {
  min-height: 40rem;
}
.logo {
  display: block;
  margin: 4rem auto 2.125rem;
  width: 3rem;
  height: 3rem;
}
p {
  margin-bottom: 1rem;
  color: #f5f3f5;
  font-size: 1.125rem;
}
.textarea {
  background: #252a32;
  border: 1px solid #3b434f;
  box-sizing: border-box;
  box-shadow: none;
  overflow: hidden;
  padding: 0.75rem 1rem;
  outline: none !important;
  color: #f5f3f5;

  &.is-danger {
    border-color: #dc495a;
  }
  &::placeholder {
    color: #647287;
    opacity: 1;
  }
  &:-ms-input-placeholder {
    color: #647287;
  }
  &::-ms-input-placeholder {
    color: #647287;
  }
  &.with-new {
    padding: 0.625rem 0.75rem;
    padding-right: 1.5rem;
  }
}
.fake {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  min-width: 100%;
  min-height: 100%;
}
.new-block .textarea {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.login-block .textarea {
  margin-bottom: 1.5rem;
}
.mnemonic-field {
  position: relative;
  margin-bottom: 1.5rem;

  .textarea {
    margin-bottom: 0;
  }

  .visible-mode {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    background-color: #252a32;
  }
}
.level {
  margin-bottom: 4rem;
}
.signup-block,
.login-block,
.new-block {
  margin: 0 auto;
  padding: 1.25rem;
  max-width: 23rem;
  min-height: 33rem;
}
.signup-block {
  padding: 2.5rem 1.5rem;
}
.danger {
  position: relative;
  background: #dc495a;
  padding: 0.75rem;

  .copy-icon {
    position: absolute;
    top: -10rem;
    right: 0.5rem;
    background-color: #252a32;
  }
}
.underline {
  display: inline-block;
  font-size: 0.825rem;
  padding-bottom: 0.25rem;
  border-bottom: 2px solid #1fba7c;
  margin-right: 0.5rem;
}
.checkbox {
  margin: 1.5rem 0;

  input {
    margin-right: 0.75rem;
  }
  &:hover {
    color: #fff;
  }
}
